import { useEffect } from 'react';
import { __ } from '@wordpress/i18n';
import { withSelect } from '@wordpress/data';
import { Disabled, Spinner } from '@wordpress/components';
const { serverSideRender: ServerSideRender } = wp;

import { tabController } from '../../../../Components/utils/functions';

import Settings from './Settings/Settings';

const Edit = props => {
	const { name, attributes, setAttributes, clientId, isSelected, allCategories } = props;

	useEffect(() => { clientId && setAttributes({ cId: clientId.substring(0, 10) }); }, [clientId]); // Set & Update clientId to cId

	useEffect(() => tabController(), [isSelected]);

	return <>
		<Settings attributes={attributes} setAttributes={setAttributes} allCategories={allCategories} />

		<Disabled>
			<ServerSideRender block={name} attributes={attributes} LoadingResponsePlaceholder={Loading} />
		</Disabled>
	</>;
};
export default withSelect(select => {
	return {
		allCategories: select('core').getEntityRecords('taxonomy', 'product_cat', { per_page: -1 })
	};
})(Edit);

const Loading = ({ children, showLoader }) => <div className={`wrpLoader ${showLoader ? 'showLoader' : ''}`}>
	{showLoader && <h3 className='wrpLoading'><Spinner /> {__('Loading...', 'recent-products')}</h3>}

	{children}
</div>