import { useState } from 'react';
import { __ } from '@wordpress/i18n';
import { InspectorControls, BlockControls, AlignmentToolbar } from '@wordpress/block-editor';
import { PanelBody, PanelRow, TabPanel, ToggleControl, RangeControl, __experimentalUnitControl as UnitControl } from '@wordpress/components';

// Settings Components
import { Label, Background, BColor, BDevice, BorderControl, ColorsControl, HelpPanel, SelectPureControl, ShadowControl, Typography } from '../../../../../Components';
import { tabController } from '../../../../../Components/utils/functions';
import { pxUnit, perUnit, emUnit } from '../../../../../Components/utils/options';

import { addArrItem, removeArrItem, filterSelected } from '../../../utils/functions';
import { generalStyleTabs } from '../../../utils/options';

const Settings = ({ attributes, setAttributes, allCategories }) => {
	const { productsPerPage, stockStatus, selectedCategories, columns, columnGap, rowGap, isImage, isTitle, isRating, isPrice, isAddToCartBtn, textAlign, productBG, productBorder, productShadow, titleTypo, titleColor, ratingColor, priceTypo, priceColor, addToCartTypo, addToCartColors, onSaleColors } = attributes;

	const [device, setDevice] = useState('desktop');

	const onStockFilterChange = (item, val) => {
		setAttributes({
			stockStatus: val ? [...addArrItem(stockStatus, item)] : [...removeArrItem(stockStatus, item)]
		});
	}

	return <>
		<InspectorControls>
			<div className='wrpInspectorInfo'>
				Need more block like this? Checkout the bundle ➡ <a href='https://wordpress.org/plugins/b-blocks' target='_blank' rel='noopener noreferrer'>B Blocks</a>
			</div>

			<TabPanel className='bPlTabPanel' activeClass='activeTab' tabs={generalStyleTabs} onSelect={() => tabController()}>{tab => <>
				{'general' === tab.name && <>
					<HelpPanel slug='recent-products-block' />


					<PanelBody className='bPlPanelBody' title={__('Query', 'recent-products')}>
						<Label className='mb5'>{__('Products Limit:', 'recent-products')}</Label>
						<RangeControl value={productsPerPage} onChange={val => setAttributes({ productsPerPage: val })} min={1} max={60} step={1} />

						<hr />

						<h3>{__('Filter by Stock', 'recent-products')}</h3>
						<ToggleControl label={__('Show In Stock Products', 'recent-products')} checked={stockStatus.includes('instock')} onChange={val => onStockFilterChange('instock', val)} />

						<ToggleControl className='mt10' label={__('Show Out of Stock Products', 'recent-products')} checked={stockStatus.includes('outofstock')} onChange={val => onStockFilterChange('outofstock', val)} />

						<ToggleControl className='mt10' label={__('Show In On Backorder Products', 'recent-products')} checked={stockStatus.includes('onbackorder')} onChange={val => onStockFilterChange('onbackorder', val)} />

						<hr />

						<h3>{__('Filter by Category', 'recent-products')}</h3>
						<Label className='mb5'>{__('Select Categories:', 'recent-products')}</Label>
						<SelectPureControl
							value={filterSelected(allCategories, selectedCategories)?.map(cat => cat)}
							onChange={val => setAttributes({ selectedCategories: val.map(cat => cat) })}
							options={allCategories?.map(cat => ({ label: `${cat.name} (${cat.count})`, value: cat.slug }))}
						/>
					</PanelBody>

					<PanelBody className='bPlPanelBody' title={__('Layout', 'recent-products')} initialOpen={false}>
						<PanelRow>
							<Label className='mb5'>{__('Columns:', 'recent-products')}</Label>
							<BDevice device={device} onChange={val => setDevice(val)} />
						</PanelRow>
						<RangeControl value={columns[device]} onChange={val => { setAttributes({ columns: { ...columns, [device]: val } }) }} min={1} max={6} step={1} beforeIcon='grid-view' withInputField={false} />

						<UnitControl className='mt20' label={__('Column Gap:', 'recent-products')} labelPosition='left' value={columnGap} onChange={val => setAttributes({ columnGap: val })} units={[pxUnit(30), perUnit(3), emUnit(2)]} isResetValueOnUnitChange={true} />

						<UnitControl className='mt20' label={__('Row Gap:', 'recent-products')} labelPosition='left' value={rowGap} onChange={val => setAttributes({ rowGap: val })} units={[pxUnit(40), perUnit(3), emUnit(2.5)]} isResetValueOnUnitChange={true} />
					</PanelBody>


					<PanelBody className='bPlPanelBody' title={__('Elements', 'recent-products')} initialOpen={false}>
						<ToggleControl label={__('Show Image', 'recent-products')} checked={isImage} onChange={val => setAttributes({ isImage: val })} />

						<ToggleControl className='mt10' label={__('Show Title', 'recent-products')} checked={isTitle} onChange={val => setAttributes({ isTitle: val })} />

						<ToggleControl className='mt10' label={__('Show Rating', 'recent-products')} checked={isRating} onChange={val => setAttributes({ isRating: val })} />

						<ToggleControl className='mt10' label={__('Show Price', 'recent-products')} checked={isPrice} onChange={val => setAttributes({ isPrice: val })} />

						<ToggleControl className='mt10' label={__('Show Add To Cart Button', 'recent-products')} checked={isAddToCartBtn} onChange={val => setAttributes({ isAddToCartBtn: val })} />
					</PanelBody>
				</>}


				{'style' === tab.name && <>
					<PanelBody className='bPlPanelBody' title={__('Product', 'recent-products')}>
						<Background label={__('Background:', 'recent-products')} value={productBG} onChange={val => setAttributes({ productBG: val })} />

						<BorderControl label={__('Border:', 'recent-products')} value={productBorder} onChange={val => setAttributes({ productBorder: val })} />

						<ShadowControl label={__('Shadow:', 'recent-products')} value={productShadow} onChange={val => setAttributes({ productShadow: val })} />
					</PanelBody>


					{isTitle && <PanelBody className='bPlPanelBody' title={__('Product Title', 'recent-products')} initialOpen={false}>
						<Typography label={__('Typography:', 'recent-products')} value={titleTypo} onChange={val => setAttributes({ titleTypo: val })} defaults={{ fontSize: { desktop: 22, tablet: 20, mobile: 18 } }} />
						<small>{__('Font Family may not work in the backend.', 'recent-products')}</small>

						<BColor label={__('Color:', 'recent-products')} value={titleColor} onChange={val => setAttributes({ titleColor: val })} defaultColor='#333' />
					</PanelBody>}


					{isRating && <PanelBody className='bPlPanelBody' title={__('Product Rating', 'recent-products')} initialOpen={false}>
						<BColor label={__('Color:', 'recent-products')} value={ratingColor} onChange={val => setAttributes({ ratingColor: val })} defaultColor='#ffdf00' />
					</PanelBody>}


					{isPrice && <PanelBody className='bPlPanelBody' title={__('Product Price', 'recent-products')} initialOpen={false}>
						<Typography label={__('Typography:', 'recent-products')} value={priceTypo} onChange={val => setAttributes({ priceTypo: val })} defaults={{ fontSize: { desktop: 14, tablet: 14, mobile: 14 }, fontWeight: 700 }} />
						<small>{__('Font Family may not work in the backend.', 'recent-products')}</small>

						<BColor label={__('Color:', 'recent-products')} value={priceColor} onChange={val => setAttributes({ priceColor: val })} defaultColor='#222' />
					</PanelBody>}


					{isAddToCartBtn && <PanelBody className='bPlPanelBody' title={__('Product Add To Cart Button', 'recent-products')} initialOpen={false}>
						<Typography label={__('Typography:', 'recent-products')} value={addToCartTypo} onChange={val => setAttributes({ addToCartTypo: val })} defaults={{ fontSize: { desktop: 15, tablet: 15, mobile: 15 }, fontWeight: 600 }} />
						<small>{__('Font Family may not work in the backend.', 'recent-products')}</small>

						<ColorsControl value={addToCartColors} onChange={val => setAttributes({ addToCartColors: val })} defaults={{ color: '#fff', bg: '#4527a4' }} />
					</PanelBody>}


					<PanelBody className='bPlPanelBody' title={__('On Sale Tag', 'recent-products')} initialOpen={false}>
						<ColorsControl value={onSaleColors} onChange={val => setAttributes({ onSaleColors: val })} defaults={{ color: '#fff', bg: '#4527a4' }} />
					</PanelBody>
				</>}
			</>}</TabPanel>
		</InspectorControls>


		<BlockControls>
			<AlignmentToolbar value={textAlign} onChange={val => setAttributes({ textAlign: val })} />
		</BlockControls>
	</>;
};
export default Settings;